.portfolio__footer {
  display: flex;
}

.portfolio__footer-content {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.portfolio__footer-content-container {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 10rem auto;
}
.portfolio__footer-content-container_signature svg {
  width: 404px;
  height: 100px;
  fill: none;
}
.portfolio__footer-content-container_social-icons {
  margin: 2rem;
}
.portfolio__footer-content-container_social-icons img {
  margin: 1rem;
}

.portfolio__footer-content-container_signature path {
  fill: none;
  stroke-width: 16;
  stroke-linecap: round;
  stroke-linejoin: round;
}

.portfolio__footer-content-text {
  width: 100%;
  display: flex;
  flex-direction: row;
  font-family: var(--font-family-text);
  color: var(--color-text);
  font-weight: 800;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: 2px;
  text-transform: capitalize;
  margin: 0;
  padding: 0;
}

.portfolio__footer-content-text-copyright {
  flex: 1;
  justify-content: flex-start;
  align-content: flex-start;
  text-align: start;
}

.portfolio__footer-content-text-credits {
  flex: 1;
  justify-content: flex-end;
  align-content: flex-end;
  text-align: end;
}

@media screen and (max-width: 1050px) {
  .portfolio__footer-content-container_signature svg {
    width: 339.36px;
    height: 84px;
    fill: none;
  }
  .portfolio__footer-content-container_social-icons {
    margin: 1.5rem;
  }
  .portfolio__footer-content-container_social-icons img {
    margin: 0 1rem;
    width: 24px;
  }
  .portfolio__footer-content-text {
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 2px;
  }
}

@media screen and (max-width: 700px) {
  .portfolio__footer-content-text-credits,
  .portfolio__footer-content-text-copyright {
    text-align: center;
    margin: 0.5rem;
  }
  .portfolio__footer-content-container_signature svg {
    width: 290.88px;
    height: 72px;
    fill: none;
  }
  .portfolio__footer-content-container_social-icons {
    margin: 1.5rem;
  }
  .portfolio__footer-content-container_social-icons img {
    margin: 0 1rem;
    width: 24px;
  }
  .portfolio__footer-content-text {
    flex-direction: column;
    font-size: 10px;
    line-height: 12px;
    letter-spacing: 1.75px;
  }
}

@media screen and (max-width: 550px) {
  .portfolio__footer-content-container_social-icons {
    margin: 0.75rem;
  }
  .portfolio__footer-content-container_social-icons img {
    margin: 0 0.5rem;
    width: 22px;
  }
  .portfolio__footer-content-text {
    font-size: 12px;
    line-height: 14px;
    letter-spacing: 1.5px;
  }
}
@media screen and (max-width: 420px) {
  .portfolio__footer-content-text-credits,
  .portfolio__footer-content-text-copyright {
    text-align: center;
    margin: 0.25rem;
  }
  .portfolio__footer-content-container_signature svg {
    width: 193.92px;
    height: 48px;
    fill: none;
  }
  .portfolio__footer-content-container_social-icons {
    margin: 0.25rem;
    margin-top: 1rem;
  }
  .portfolio__footer-content-container_social-icons img {
    margin: 0 rem;
    width: 16px;
  }
  .portfolio__footer-content-text {
    flex-direction: column;
    font-size: 10px;
    line-height: 12px;
    letter-spacing: 1.75px;
  }
}

