.stroke-d {
  stroke-dasharray: 520;
}
.stroke-divy {
  stroke-dasharray: 4400;
}
.stroke-am {
  stroke-dasharray: 1200;
}
.stroke-i-dot {
}
.stroke-j-dot {
}
.stroke-p-i-dot {
}
.stroke-p {
  stroke-dasharray: 1600;
}
.stroke-p-line {
  stroke-dasharray: 660;
}
.stroke-raja {
  stroke-dasharray: 2350;
}
.stroke-pati {
  stroke-dasharray: 2350;
}
.stroke-t-dash {
  stroke-dasharray: 260;
}

@media (prefers-reduced-motion: no-preference) {
  .stroke-d-anim {
    animation: d var(--signature-anim-time) normal linear;
  }
  .stroke-divy-anim {
    animation: divy var(--signature-anim-time) normal linear;
  }
  .stroke-am-anim {
    animation: am var(--signature-anim-time) normal linear;
  }
  .stroke-i-dot-anim {
    animation: i-dot var(--signature-anim-time) normal linear;
  }
  .stroke-j-dot-anim {
    animation: j-dot var(--signature-anim-time) normal linear;
  }
  .stroke-p-i-dot-anim {
    animation: p-i-dot var(--signature-anim-time) normal linear;
  }
  .stroke-p-anim {
    animation: p var(--signature-anim-time) normal linear;
  }
  .stroke-p-line-anim {
    animation: p-line var(--signature-anim-time) normal linear;
  }
  .stroke-raja-anim {
    animation: raja var(--signature-anim-time) normal linear;
  }
  .stroke-pati-anim {
    animation: pati var(--signature-anim-time) normal linear;
  }
  .stroke-t-dash-anim {
    animation: t-dash var(--signature-anim-time) normal linear;
  }
}

@keyframes d {
  0% {
    stroke-dashoffset: 520;
  }
  16.665% {
    stroke-dashoffset: 0;
  }
}

@keyframes divy {
  0%,
  16.665% {
    stroke-dashoffset: 4400;
  }
  33.33% {
    stroke-dashoffset: 0;
  }
}

@keyframes am {
  0%,
  33.5% {
    stroke-dashoffset: 1200;
  }
  50% {
    stroke-dashoffset: 0;
  }
}

@keyframes i-dot {
  0%,
  33.5% {
    transform: translate3d(0, -200px, 0);
    visibility: hidden;
  }
  35% {
    visibility: visible;
  }
  50% {
    visibility: visible;
    transform: translate3d(0, 0, 0);
  }
}

@keyframes j-dot {
  0%,
  70% {
    transform: translate3d(0, -200px, 0);
    visibility: hidden;
  }
  80% {
    visibility: visible;
  }
  90% {
    visibility: visible;
    transform: translate3d(0, 0, 0);
  }
}

@keyframes p-i-dot {
  0%,
  90% {
    transform: translate3d(0, -200px, 0);
    visibility: hidden;
  }
  94% {
    visibility: visible;
  }
  100% {
    visibility: visible;
    transform: translate3d(0, 0, 0);
  }
}

@keyframes p {
  0%,
  50% {
    stroke-dashoffset: 1600;
  }
  60% {
    stroke-dashoffset: 0;
  }
}

@keyframes p-line {
  0%,
  50% {
    stroke-dashoffset: 660;
  }
  60% {
    stroke-dashoffset: 0;
  }
}

@keyframes raja {
  0%,
  60% {
    stroke-dashoffset: 2350;
  }
  70% {
    stroke-dashoffset: 0;
  }
}

@keyframes pati {
  0%,
  70% {
    stroke-dashoffset: 2350;
  }
  100% {
    stroke-dashoffset: 0;
  }
}

@keyframes t-dash {
  0%,
  96% {
    stroke-dashoffset: 260;
  }
  100% {
    stroke-dashoffset: 0;
  }
}
