.portfolio__Skills {
  padding: 6rem 0;
  display: flex;
}

.portfolio__skills-content {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.portfolio__skills-content h1 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 48px;
  line-height: 64px;
  letter-spacing: 0.05em;
}
.portfolio__skills-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 6rem 5rem;
}

.portfolio__skills-container div {
  width: 6rem;
  height: 6rem;
  /* background: rgba(255,255,255,0.20); */
  background: transparent;
  box-shadow: 0px 5px 8px var(--color-lgb);
  margin: 1.5rem;
  border-radius: 50%;
  padding: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.portfolio__skills-container div:hover {
  box-shadow: 0px 0px 8px var(--color-black);
}

.portfolio__skills-container div img {
  height: 72px;
  width: 72px;
  max-width: 80%;
  max-height: 80%;
}

@media screen and (max-width: 1050px) {
  .portfolio__skills-container div {
    width: 4rem;
    height: 4rem;
    margin: 1rem;
    padding: 1rem;
  }
  .portfolio__skills-container {
    margin: 4rem 3rem;
  }
  .portfolio__skills-container div img {
    height: 64px;
    width: 64px;
    max-width: 80%;
    max-height: 80%;
  }
  .portfolio__skills-content h1 {
    font-size: 36px;
    line-height: 44px;
    letter-spacing: 0.05em;
  }
}
@media screen and (max-width: 700px) {
  .portfolio__skills-container div {
    width: 3rem;
    height: 3rem;
    margin: 0.75rem;
    padding: 0.75rem;
  }
  .portfolio__skills-container {
    margin: 4rem 4rem;
  }
  .portfolio__skills-container div img {
    height: 64px;
    width: 64px;
    max-width: 80%;
    max-height: 80%;
  }
  .portfolio__skills-content h1 {
    font-size: 28px;
    line-height: 32px;
    letter-spacing: 0.05em;
  }
}
@media screen and (max-width: 550px) {
  .portfolio__skills-container div {
    width: 3rem;
    height: 3rem;
    margin: 0.5rem;
    padding: 0.5rem;
  }
  .portfolio__skills-container {
    margin: 2rem 1rem;
  }
  .portfolio__skills-container div img {
    height: 48px;
    width: 48px;
    max-width: 80%;
    max-height: 80%;
  }
  .portfolio__skills-content h1 {
    font-size: 24px;
    line-height: 28px;
    letter-spacing: 0.05em;
  }
}
